* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch__minutes-hand {
  z-index: 1;
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch__seconds-hand {
  z-index: 2;
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s infinite rotate;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center-mark {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite rotate;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) rotate;
}
